<template>
<v-container>
  <v-simple-table>    
    <template v-slot:default>
      <thead>
        <tr bgcolor="lightgrey">
          <th class="text-center">Job ID</th>
          <th class="text-center">Job Name</th>
          <th class="text-center">Parameters</th>
          <th class="text-center">Start Time</th>
          <th class="text-center">End Time</th>
          <th class="text-center">Exit Code</th>          
        </tr>
      </thead>
      <tbody>
        <tr v-for="job in jobs" :key="job.id">
          <td class="text-center">{{ job.instanceId }}</td>
          <td class="text-left">{{ job.name }}</td>
          <td class="text-right">{{ job.params }}</td> 
          <td class="text-center">{{ job.execution.startTime }}</td> 
          <td class="text-center">{{ job.execution.endTime }}</td>          
          <td class="text-center" v-if="job.execution.exitCode=='FAILED' || job.execution.exitCode=='STOPPED' || job.execution.exitCode=='UNKNOWN'">
            <v-badge
              color="error"
              icon="mdi-bell"
              left
              overlap
            >
              <v-btn text 
                small
                color=red
                @click="viewStep(job)"
              >{{ job.execution.exitCode }}
              </v-btn>
            </v-badge>
          </td>
          <td class="text-center" v-else>
            <v-btn text 
              small
              @click="viewStep(job)"
            >{{ job.execution.exitCode }}
            </v-btn>   
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
  <div class="text-center">
    <v-pagination
      v-model="page"
      :length="numOfPage"
      :total-visible="10"
      @input="findByAll(page-1)"
    ></v-pagination>
  </div>
</v-container> 
</template>
<script>
import axios from "axios"

export default {
  data () {
    return {
      jobs: [],
      pageSize: 10,
      numOfPage: 0,
      page: 1
    }
  },
  created() {
    this.findByAll(this.page-1)
  },
  methods: {
    findByAll(page) {
      axios.get(`/job/all?page=${page}&size=${this.pageSize}`)
        .then(res => { 
          this.jobs = res.data.content
          this.numOfPage = Math.ceil(res.data.totalElements / this.pageSize)
        })
        .catch(err => {
          console.error(err)
        })
    },
    viewStep(job) {
      // step으로 이동
      this.$router.push({ name: 'step', params:job.execution })
    }
  }
}
</script>